.weekly-survey-dialog {
  min-width: 500px;
  max-width: 700px;

  @media (max-width: 600px) {
    min-width: 100%;
  }

  .dialog-title {
    display: flex;
    align-items: center;
    gap: 12px;
    padding: 20px 24px 16px;
    margin: 0;
    border-bottom: 1px solid #e0e0e0;

    .week-badge {
      background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
      color: white;
      padding: 6px 14px;
      border-radius: 20px;
      font-size: 14px;
      font-weight: 600;
    }

    .title-text {
      font-size: 20px;
      font-weight: 600;
      color: #333;
    }
  }

  .dialog-content {
    padding: 24px;
    max-height: 60vh;
    overflow-y: auto;
  }

  .loading-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 40px;
    gap: 16px;

    p {
      color: #666;
      margin: 0;
    }
  }

  .already-responded {
    text-align: center;
    padding: 40px 20px;

    .check-icon {
      font-size: 64px;
      width: 64px;
      height: 64px;
      color: #4caf50;
    }

    h3 {
      color: #333;
      margin: 16px 0 8px;
    }

    p {
      color: #666;
      margin: 0;
      line-height: 1.6;
    }
  }

  .error-container {
    text-align: center;
    padding: 40px 20px;

    .error-icon {
      font-size: 64px;
      width: 64px;
      height: 64px;
      color: #f44336;
    }

    p {
      color: #f44336;
      margin: 16px 0 0;
    }
  }

  .survey-form {
    .survey-description {
      background: #f5f5f5;
      padding: 16px;
      border-radius: 8px;
      color: #666;
      margin-bottom: 24px;
      line-height: 1.6;
    }

    .questions-container {
      display: flex;
      flex-direction: column;
      gap: 24px;
    }

    .question-item {
      background: #fafafa;
      border-radius: 12px;
      padding: 20px;
      border: 1px solid #e0e0e0;

      .question-header {
        display: flex;
        align-items: flex-start;
        gap: 10px;
        margin-bottom: 16px;

        .question-number {
          background: #667eea;
          color: white;
          padding: 4px 10px;
          border-radius: 4px;
          font-size: 12px;
          font-weight: bold;
          flex-shrink: 0;
        }

        .question-text {
          flex: 1;
          font-weight: 500;
          color: #333;
          line-height: 1.5;
        }

        .required-badge {
          background: #ff5252;
          color: white;
          padding: 2px 8px;
          border-radius: 4px;
          font-size: 11px;
          flex-shrink: 0;
        }
      }

      .question-input {
        .full-width {
          width: 100%;
        }

        .radio-group {
          display: flex;
          flex-direction: column;
          gap: 8px;

          mat-radio-button {
            padding: 8px 12px;
            background: white;
            border-radius: 8px;
            transition: all 0.2s;

            &:hover {
              background: #f0f0f0;
            }
          }
        }

        .checkbox-group {
          display: flex;
          flex-direction: column;
          gap: 8px;

          mat-checkbox {
            padding: 8px 12px;
            background: white;
            border-radius: 8px;
            transition: all 0.2s;

            &:hover {
              background: #f0f0f0;
            }
          }
        }

        .rating-container {
          display: flex;
          align-items: center;
          gap: 16px;
          padding: 12px;
          background: white;
          border-radius: 8px;

          .rating-stars {
            display: flex;
            gap: 4px;
          }

          .star-button {
            background: none;
            border: none;
            cursor: pointer;
            padding: 4px;
            transition: transform 0.15s;

            mat-icon {
              font-size: 32px;
              width: 32px;
              height: 32px;
              color: #e0e0e0;
            }

            &.selected mat-icon,
            &:hover mat-icon {
              color: #ffc107;
            }

            &:hover {
              transform: scale(1.1);
            }
          }

          .rating-value {
            font-size: 18px;
            font-weight: 600;
            color: #667eea;
          }
        }

        .scale-container {
          padding: 12px;
          background: white;
          border-radius: 8px;

          .scale-labels {
            display: flex;
            justify-content: space-between;
            margin-bottom: 8px;
            font-size: 12px;
            color: #666;
          }

          mat-slider {
            width: 100%;
          }

          .scale-value {
            text-align: center;
            margin-top: 8px;
            font-size: 14px;
            color: #667eea;
            font-weight: 500;
          }
        }
      }
    }
  }

  mat-dialog-actions {
    padding: 16px 24px;
    border-top: 1px solid #e0e0e0;
    gap: 12px;

    .button-spinner {
      display: inline-block;
    }

    button[mat-raised-button] {
      min-width: 100px;
    }
  }
}
